$navbar-background: #D9D9D9;
$languages-color: #333;
$font-family: Roboto;
$red-color: #BF0F0F;
$grey-color: #C4C4C466;
$light-grey-color: #f4f4f4;

$dark-color: #333;
$light-color: #fff;

$nav-dark: #828282;

//  General styling  //////////////////////////////////
* {
margin: 0;
padding: 0;
box-sizing: border-box;
overflow-x: hidden;
}

body {
font-family: $font-family;
overflow-x: hidden;
}

a {
text-decoration: none;
}

li {
list-style: none;
}

// Modal //////////////////////////////
body {
  position: relative;
}

.modal {
  display: none;
}

.modal.active {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #333;
  opacity: 0.8;
  z-index: 1;
  display: flex;
  justify-content: center;
  padding-top: 3rem;

    .modal-window {
      display: flex;
      flex-direction: column;
      position: fixed;

        .modal-header {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          justify-content: flex-end;
          padding: 10px;
          background-color: $navbar-background;
          border-radius: 5px 5px 0px 0px;
            i:hover {
              cursor: pointer;
            }
        }

        .modal-body {
          text-align: center;
          padding: 3rem 5rem;
          background-color: #fff;
        }

        .modal-footer {
          background-color: $navbar-background;
          display: flex;
          flex-direction: row;
          align-items: flex-start;
          justify-content: flex-start;
          padding: 10px;
          border-radius: 0px 0px 5px 5px;
             .modal-btn {
               background-color: $red-color;
               color: #fff;
               border: 1px solid #fff;
               border-radius: 5px;
               padding: 0.4rem 0.7rem;
             }
             .modal-btn:hover {
              cursor: pointer;
             }
        }
    }
}

