// hamburger menu /////////////////////////////////
.hamburger {
  width: 63px;
  height: 54px;
  border: 2px solid $red-color;
  border-radius: 5px;
  margin-top: 1rem;
  padding-top: 0.7rem;
  padding-left: 0.2rem;

    .ham-line {
      width: 50px;
      height: 4px;
      border-radius: 10px;
      background-color: $red-color;
    }

    .ham-line-1,
    .ham-line-2 {
      margin-bottom: 0.5rem;
    }

    .ham-line-1,
    .ham-line-3 {
      transition: transform 0.5s ease-in;
    }
}

// Active classes for hamburger
.hamburger.active {
  border: 1px solid $navbar-background;
}

.ham-line-2.active {
   display: none;
}

.ham-line-1.active {
   transform: rotate(45deg);
    transform-origin: top left;
    margin-left: 5px;
}
.ham-line-3.active {
  transform: rotate(-45deg);
  margin-top: 1rem;
  margin-right: 1rem;
}

.hamburger-nav {
  position: absolute;
  top: 0%;
  right: 5%;
  display: none;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  z-index: 1;
  padding-top: 0.5rem;
  opacity: 0;
  transition: opacity 0.4s ease-in;
  background-color: $red-color;

    .ham-link {
      display: flex;
      flex-direction: column;
      padding: 0.5rem;
      color: #fff;
      border-bottom: 1px solid #fff;
      margin-bottom: 0.7rem;

        a {
          color: #fff;
        }
    }
}

.hamburger-nav.active {
  display: flex;
  opacity: 0.9;
}

