// Showcase  ///////////////////////////////////
.showcase {
  position: relative;
}

.showcase #chevron-left {
  position: absolute;
  color: $red-color;
  top: 40%;
  left: 4%;
  font-size: 3rem;
}

.showcase #chevron-right {
  position: absolute;
  color: $red-color;
  top: 40%;
  right: 4%;
  font-size: 3rem;
}

.showcase {
  img {
    width: 100%;
    height: 75vh;
    display: none;
    opacity: 0;
    object-fit: cover;
  }

  img.active {
    display: block;

   animation-name: animate-image;
   animation-direction: alternate-reverse;
   animation-timing-function: ease-in-out;
   animation-duration: 0.4s;
   animation-fill-mode: forwards;
  }
}

@keyframes animate-image {
  from {
    opacity: 1
  } to {
    opacity: 0;
  }
}

.showcase .circles {
  position: absolute;
  left: 45%;
  bottom: 5%;
  display: flex;
  flex-direction: row;
  }
  
  .circles .circle-1,
  .circles .circle-2,
  .circles .circle-3,
  .circles .circle-4 {
    width: 15px;
    height: 15px;
    border: 1px solid $red-color;
    border-radius: 50%;
    margin-left: 0.4rem;
    margin-right: 0.4rem;
  }
  
  .circles .circle-1:hover,
  .circles .circle-2:hover,
  .circles .circle-3:hover,
  .circles .circle-4:hover,
  .showcase #chevron-left:hover,
  .showcase #chevron-right:hover {
    cursor: pointer;
  }
  
  .circles .circle-1.active,
  .circles .circle-2.active,
  .circles .circle-3.active,
  .circles .circle-4.active {
    background-color: $red-color;
  }

